body {
    background-image: url("./possessed-photography-tOjIx_NyzFo-unsplash.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    font-family: 'Calculator', sans-serif;
}

#calculator {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

button {
    width: 100px;
    height: 100px;
    font-size: 24px;
    transition: transform 01.s ease-in-out;
    font-family: inherit;
    border: 1px solid black;
}
button:active {
    transform: scale(0.95);
}

#display {
    font-size: 45px;
    background-color:black;
    border: 1px solid white;
    color:antiquewhite;
    height: 15vh;
    text-align: right;
    font-weight: 600;
    color:	#149414 ;
    animation: color-change 1s infinite alternate linear;
    overflow-y: auto;
    max-width: 400px;
    opacity: 0.95;
}
@keyframes color-change {
    0% {
      color: #0d0208;
    }
    10% {
      color: #003b00;
    }
    50% {
      color: #008f11;
    }
    100% {
      color: #00ff41;
    }
  }
.inner-div {
  box-sizing: border-box;
  width: 100%;
  word-wrap: break-word;
  overflow-wrap: break-word;
  white-space: pre-wrap;
  max-width: 100%;
}


.content {
    display: grid;
    grid-template-areas: 
    "display display display display"
    "clear clear clear divide"
    "seven eight nine multiply"
    "four five six subtract"
    "one two three add"
    "zero zero decimal equals";
    grid-template-columns: 1fr 1fr 1fr 1fr;
}

#display {
    grid-area: display;
}
#clear {
    grid-area: clear;
    width: 100%;
    background-color:firebrick;
}
#divide {
    grid-area: divide;
    background-color: dodgerblue;
}
#seven {
    grid-area: seven;
}
#eight {
    grid-area: eight;
}
#nine {
    grid-area: nine;
}
#multiply {
    grid-area: multiply;
    background-color: dodgerblue;
}
#four {
    grid-area: four;
}
#five {
    grid-area: five;
}
#subtract {
    grid-area: subtract;
    background-color: dodgerblue;
}
#one {
    grid-area: one;
}
#two {
    grid-area: two;
}
#three {
    grid-area: three;
}
#add {
    grid-area: add;
    background-color: dodgerblue;
}
#zero {
    grid-area: zero;
    width: 100%;
}
#decimal {
    grid-area: decimal;
}
#equals {
    grid-area: equals;
}

@media only screen and (min-width: 343px) and (max-width: 450px) {
    button {
        width: 70px;
        height: 70px;
        font-size: 24px;
        transition: transform 01.s ease-in-out;
        font-family: inherit;
        border: 1px solid black;
    }
    #display {
        font-size: 35px;
        max-width: 275px;
    }
  }